:root {
    /* border radiuses */
    --br-11xl: 30px;
    --br-8xs: 5px;
    --br-xl: 20px;
    /* fonts */
    --font-inter: Inter;

    /* font sizes */
    --font-size-mini: 0.94rem;

    /* Colors */
    --color-midnightblue-100: #1c0e44;
    --color-midnightblue-200: #190c40;
    --color-darkslateblue: #484a92;

    /* border radiuses */
    --br-31xl: 50px;
}


@media (max-width:400px) {
    .kesariya-parent {
        font-family: "Inter";
        position: absolute;
        top: 51%;
        left: 24%;
        width: 9.69rem;
        height: 5.38rem;
    }

    .mobile-player-layer-2-parent {
        font-family: 'Inter';
    }

    .test-player {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 92vh;
        overflow: hidden;


    }

    .kesariya-7-icon {
        top: -14%;
        width: 80%;
    }

    .player-ellpi-32-icon {
        position: absolute;
        top: -16.25%;
        left: 36%;
        width: 75%;
        height: 103%;
    }

    .test-player-grp-41-icon {
        background-image: url("../public/public/Vector\ 30.png");
        background-repeat: no-repeat;
        background-size: contain;
        position: relative;
        top: 67.5%;
        left: 0%;
        width: 100%;
        height: 60%;
        bottom: 0%;
    }

    .test-player-nextbtn-mobi1 {
        position: relative;
        top: 11%;
        left: 60%;
        width: 10%;
        transform: rotate(180deg);
    }

    .test-player-nextbtn-mobi2 {
        position: relative;
        top: 11%;
        left: 78%;
        width: 10%;
        transform: rotate(180deg);
    }

    .test-player-mobi-playpause {
        position: relative;
        top: 19%;
        left: 32%;
        width: 53%;

    }

    .test-player-mobi-vectpause {
        position: relative;
        top: 28%;
        left: 25%;
        width: 53%;

    }

    .test-sound-wave-progress {
        position: absolute;
        width: 54.7%;
        height: 5%;
        top: 19%;
        display: flex;
        left: 1%;
        align-items: center;
    }

    .div-items {
        background-color: #484a92;
        height: 40px;
        width: 10px;
        margin-left: 5px;
        border-radius: 16px;

    }

    .animate-lap-sound {
        animation: animate 1.2s linear infinite;

    }

    @keyframes animate {
        50% {
            height: 20%;

        }

        100% {
            height: 100%;
        }

    }

    .div-items:nth-child(1) {
        animation-delay: 0s;
        height: 35px;
    }

    .div-items:nth-child(2) {
        animation-delay: .15s;
        height: 25px;
    }

    .div-items:nth-child(3) {
        animation-delay: 0.3s;
        height: 35px;
    }

    .div-items:nth-child(4) {
        animation-delay: .45s;
        height: 27px;
    }

    .div-items:nth-child(5) {
        animation-delay: .6s;
        height: 15px;
    }

    .div-items:nth-child(6) {
        animation-delay: .75s;
        height: 22px;
    }

    .div-items:nth-child(7) {
        animation-delay: 0.9s;
        height: 40px;
    }

    .div-items:nth-child(8) {
        animation-delay: 0.75s;
        height: 19px;
    }

    .div-items:nth-child(9) {
        animation-delay: 0.6s;
        height: 32px;
    }

    .div-items:nth-child(10) {
        animation-delay: 0.45s;
        height: 21px;
    }

    .div-items:nth-child(11) {
        animation-delay: 0.3s;
        height: 20px;
    }

    .div-items:nth-child(12) {
        animation-delay: 0s;
        height: 18px;
    }

    .test-width {
        position: absolute;
        width: 180px;
        height: 40px;
        top: 16.4%;
        display: flex;
        left: 1%;
        align-items: center;
        overflow: hidden;

    }

    .test-width .div-items {
        background-color: white;
    }

    .test-play-pause-container {
        position: absolute;
        top: 10%;
        left: 74%;
        width: 11%;
        height: 10%;
        border-radius: 50%;
        background-color: white;
    }

    .test-ellip {
        position: absolute;
        top: -1%;
        left: -4%;
        width: 100%;
        height: 15%;
        overflow: hidden;
    }

    .test-grp-45-icon {
        position: absolute;
        top: 85%;
        left: 3%;
        width: 90%;

    }
}